}

.nav-button {
  width: 100%;
  height: 100%;
  background-color: lightblue;
  border-width: 0;
  border-radius: 0 !important;
  margin-right: 0;
  margin: 0 !important;
}

.nav-button:hover {
  background-color: royalblue;
  color: white;
}




